<template>
    <div>
        <el-table
                :data="measuredList"
                :fit="true"
                lazy>

            <el-table-column
                    label="日期"
                    prop="date"
                    align="center"
                    sortable
                    :formatter="getDateStringFromTimestamp"
            ></el-table-column>
            <el-table-column
                    label="水库"
                    prop="reservoirName"
                    align="center"
                    :filters="filters.reservoir"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    label="卫星名称"
                    prop="satelliteName"
                    align="center"
                    :filters="filters.reservoir"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    label="经度"
                    prop="lng"
                    align="center"
            ></el-table-column>
            <el-table-column
                    label="纬度"
                    prop="lat"
                    align="center"
            ></el-table-column>
            <el-table-column
                    label="水位"
                    prop="level"
                    header-align="center"
                    align="center"
                    sortable
            ></el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: "dataManagementRadarTable",
        data: function () {
            return {
                measuredList: [],
                filters:{
                    reservoir:[],
                }
            }

        },
        methods: {
            getDateStringFromTimestamp: function(row){
                return new Date(row.date).toLocaleDateString();
            },
            getMeasuredStorage: function(row){
                if (row.measuredStorage*1 === 0) return '-';
                return row.measuredStorage
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value+'';
            }
        },
        mounted() {
            const self = this;
            this.axios.get('/api/data/radar').then((response) => {
                if (response.status === 200) {
                    this.measuredList = response.data;
                    for (let i = 0; i<this.measuredList.length;i++){
                        if (this.filters.reservoir.find((x)=>{
                            return x.text===this.measuredList[i].siteName
                        }) === undefined){
                            self.filters.reservoir.push({
                                text: this.measuredList[i].siteName,
                                value: this.measuredList[i].siteName,
                            })
                        }
                    }
                } else {
                    console.log('error');
                }
            })
        }
    }
</script>

