<template>
    <div>
        <el-table
                :data="measuredList"
                :fit="true"
                style="max-height: 100%">

            <el-table-column
                    label="日期"
                    prop="date"
                    align="center"
                    sortable
                    :formatter="getDateStringFromTimestamp"
            ></el-table-column>
            <el-table-column
                    label="水库"
                    prop="siteName"
                    align="center"
                    :filters="filters.reservoir"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    label="水位"
                    prop="waterLevel"
                    header-align="center"
                    align="center"
                    sortable
            ></el-table-column>
            <el-table-column
                    label="蓄水量"
                    prop="measuredStorage"
                    header-align="center"
                    align="center"
                    :formatter="getMeasuredStorage"
                    sortable
            ></el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: "dataManagementMeasuredTable",
        data: function () {
            return {
                measuredList: [],
                filters:{
                    reservoir:[],
                }
            }

        },
        methods: {
            getDateStringFromTimestamp: function(row){
                return new Date(row.date).toLocaleDateString();
            },
            getMeasuredStorage: function(row){
                if (row.measuredStorage*1 === 0) return '-';
                return row.measuredStorage
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value+'';
            }
        },
        mounted() {
            const self = this;
            this.axios.get('/api/data/measured').then((response) => {
                if (response.status === 200) {
                    this.measuredList = response.data;
                    for (let i = 0; i<this.measuredList.length;i++){
                        if (this.filters.reservoir.find((x)=>{
                            return x.text===this.measuredList[i].siteName
                        }) === undefined){
                            self.filters.reservoir.push({
                                text: this.measuredList[i].siteName,
                                value: this.measuredList[i].siteName,
                            })
                        }
                    }
                } else {
                    console.log('error');
                }
            })
        }
    }
</script>

